.form-container {
  height: 100%;
  width: 100%;
}

h2 {
  position: absolute;
  left: 20%;
}

Form {
  height: 100%;
  width: 100%;
  position: absolute;
  top: 20px;
  left: 10%;
  margin: 50px auto;
  max-width: 420px;
  margin: 50px auto;
}

Input {
  font-family: Helvetica, Arial, sans-serif;
  font-weight: 500;
  font-size: 18px;
  border-radius: 5px;
  line-height: 22px;
  background-color: transparent;
  border: 2px solid #cc6666;
  transition: all 0.3s;
  padding: 13px;
  margin-bottom: 15px;
  width: 100%;
  box-sizing: border-box;
  outline: 0;
}

.content {
  height: 150px;
  line-height: 150%;
  resize: vertical;
  background-color: transparent;
  border: 2px solid #cc6666;
  transition: all 0.3s;
  padding: 13px;
  margin-bottom: 15px;
  width: 100%;
  box-sizing: border-box;
  border-radius: 5px;
}

Button {
  font-family: "Montserrat", Arial, Helvetica, sans-serif;
  width: 100%;
  background: #cc6666;
  border-radius: 5px;
  border: 0;
  cursor: pointer;
  color: white;
  font-size: 24px;
  padding-top: 10px;
  padding-bottom: 10px;
  transition: all 0.3s;
  margin-top: -4px;
  font-weight: 700;
}
Button:hover {
  background: #cc4949;
}

@media only screen and (max-width: 780px) and (max-height: 900px) {
    Form {
        max-width: 300px;
        left: 20%;
    }
}